<template>
  <div id="todos">
    <span>해야할 일</span>
    <div class="todo-list">
      <transition-group enter-active-class="uk-animation-slide-top-medium" leave-active-class="uk-animation-slide-bottom-small uk-animation-reverse">
        <div class="content" v-for="content of this.$store.state.todos" :key="content.id" @click="removeTodo(content.id)">
            <span class="content-title">{{ content.title }}</span>
            <br>
            <span class="content-subtitle"> {{ content.date }} </span>
        </div>
        </transition-group>
    </div>

    <div id="input-field" class="ui mini action input">
      <input v-model="input_todo" type="text" placeholder="해야할 일 입력하기" @keyup.enter="addTodo()">
      <button id="add-todo" class="ui teal right icon button" @click="addTodo()">
      <i class="add icon"></i>
      </button>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Todos',

  data() {
    return {
      input_todo: '',
    }
  },

  methods: {
    getTodoId() {
      return this.$store.state.todos.length ? Math.max(...this.$store.state.todos.map(todo => todo.id)) + 1 : 1
    },

    addTodo(event) {
      if (this.input_todo !== '') {
        const date = new Date();
        const _todo = { id: this.getTodoId(), title: this.input_todo, completed: false, date: `${date.getFullYear()}년 ${date.getMonth() + 1}월 ${date.getDate()}일 ${date.getHours()}시 ${date.getMinutes()}분에 추가함` };

        this.$store.commit('addTodo', _todo);
      };
      
      this.input_todo = '';

      return;
    },

    removeTodo(todo_id) {
      if (!todo_id) return;

      this.$store.commit('removeTodo', todo_id);
    }
  }
}
</script>

<style scoped>
  #todos {
    border: 3rem solid transparent;
    border-image: url('../../assets/card_background.png');
    border-image-slice: 50 50 120 50 fill;
  }
  
  #todos>span {
    text-align: left;
    font-weight: 500;
    font-size: 1.4rem;
    padding: 0px 0px 0px 5px;
    margin-bottom: 1.43rem;
  }
  
  input {
    border: 1px solid rgb(56, 223, 220);
  }

  .content {
    list-style: none; 
    padding: 0.7rem 1rem 0.7rem 1rem;
    margin-top: 1rem;
    border: 2px solid rgba(187,198,206, 0.4);
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 8px 6px -6px rgba(192,197,208, 0.5);
    line-height: 1.5;
    box-sizing: border-box;
  }

  .content-subtitle {
  font-size: 0.9rem;
  /* font-size: 18px; */
  color: rgb(182, 179, 179);
  font-weight: 400;
}

  #input-field {
    width: 100%;
    margin-top: 1.4rem;
  }

  #add-todo {
    background: rgb(57, 197, 240);
  }
</style>





