<template>
    <div id="notice" class="notice-list-view">
      <div class="notice-list-view-title">
        {{ title }}
      </div>

      <div class="content-wrapper">
        <transition-group enter-active-class="uk-animation-slide-bottom-medium">
        <li class="content" v-for="content of notice_contents" :key="content.id">
            <span class="content-title">{{ content.title }}</span>
            <br>
            <span class="content-subtitle">{{ content.subtitle }}</span>
        </li>
        </transition-group>
      </div>
      <div class="footer">
        <transition name="button-toggle">
          <button @click="getMoreInfo()" class="detail-button">
            <span :key="isClicked">{{ !isClicked ? `${this.$store.state.notice_contents.length - notice_contents.length}개의 소식 더 보기` : `접어두기` }}</span>
          </button>
        </transition>
      </div>
    </div>
<!-- :style="isClicked ? { transition: `height 1s ease`, height: `700px` } : ''" -->
</template>

<script>
  export default {
    name: 'NoticeListViewComponent',

    data() {
      return {
        title: '나에게 필요한 정보',
        notice_contents: this.$store.state.notice_contents.slice(0,3),

        isClicked: false
      };
    },

    methods: {
      getMoreInfo() {
        if(this.isClicked === false) {
          this.notice_contents = this.$store.state.notice_contents;
          this.toggleClick();
        } else {
          this.notice_contents = this.$store.state.notice_contents.slice(0,3);
          this.toggleClick();
        }
      },

      toggleClick() {
        this.isClicked = !this.isClicked;
      }
    },
  };
</script>

<style lang="scss" scoped>
$content-padding-top: 1rem;
$content-padding-left: 1.3rem;
$contetn-margin-top: 1rem;

.button-toggle-enter {
  // transition: all .5s ease;
  // transform: translateX(100%);
}

.notice-list-view-clicked {
  transition: background-color 1s ease;
  background-color: rgba(77, 77, 77, 0.4);
}

/* Make as card */
.notice-list-view {
  border: 3rem solid transparent;
  border-image: url('../../assets/card_background.png');
  border-image-slice: 50 50 120 50 fill;
}

.notice-list-view-title {
  text-align: left;
  font-weight: 500;
  font-size: 1.4rem;
  padding: 0px 0px 0px 5px;
  margin-bottom: 1.43rem;
}

.content {
  list-style: none; 
  padding: 0.7rem 1rem 0.7rem 1rem;
  margin-top: 1rem;
  border: 2px solid rgba(187,198,206, 0.4);
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 8px 6px -6px rgba(192,197,208, 0.5);
  line-height: 1.5;
  box-sizing: border-box;
}

.content-title {
  font-size: 1.1rem;
  font-weight: 700;
  color: rgb(26, 26, 26);
}

.content-subtitle {
  font-size: 0.9rem;
  /* font-size: 18px; */
  color: rgb(182, 179, 179);
  font-weight: 400;
}

.footer {
  margin-top: 1.43rem;
}

.detail-button {
  width: 100%;
  // box-shadow: 0 4px 8px 2px rgba(0,0,0,0.15);
  // border-radius: 8px;
  // border: 2px solid rgba(9, 179, 255, 0.4);
  // height: 3.6rem;
  // margin-bottom: 0.5rem;
  font-size: 1.1rem;
  color: rgb(9, 179, 255);
  background: none;
  border: none;
  // background: #fff;
}

.detail-button:active {
  border: none;
  background: none;
  color: rgb(233, 85, 85);
}
</style>



